import { mensajeOK, mensaje } from "./funcionesUtiles";

const subirBDD = async () => {
    const resp = await fetch("../json/productos.json");
    const productos = await resp.json();

    const respJSON = await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/db?option=uploadDB`,
            {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',         //Si los datos a enviar son json
                },
                body: JSON.stringify(productos),
                credentials: "include"                          //Para poder recibir cookies
            }
        );

    const resolution = await respJSON.json();
    resolution.success ? mensajeOK("Base de datos subida correctamente (Se vaciaron todos los carritos)") : mensaje("Error al subir base de datos", "hold");
}

const leerProducto = async (id) => {                                         //Buscamos y leemos un producto por id en la BDD
    const respJSON = await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/db?option=getProduct`,
        {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',         //Si los datos a enviar son en formato json
            },
            body: JSON.stringify({id}),
            credentials: "include"                          //Para poder recibir cookies
        }
    );

    const productoDB = await respJSON.json();
    const producto = [productoDB._id, productoDB];                                //producto es un array de 2 posiciones, la primera tiene el id del producto (generado automaticamente por firebase)
    return producto;                                                        //la segunda  posición tiene los datos del producto obtenidos con el método data()
}

const actualizarProductoEnBDD = async (id, field, value) => {
    const respJSON = await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/db?option=modifyProduct`,
        {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',         //Si los datos a enviar son en formato json
            },
            body: JSON.stringify({id, field, value}),
            credentials: "include"                          //Para poder recibir cookies
        }
    );

    const resolution = await respJSON.json();
    resolution.success ? console.log("Producto actualizado Correctamente") : console.log("Error al actualizar producto");
}

const leerBDD = async () => {
    const respJSON = await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/db?option=getBDD`,
        {
            method: 'POST',
            credentials: "include"                          //Para poder recibir cookies
        }
    );

    const productosBDD = await respJSON.json();
    const BDDEnArray = productosBDD.map((producto) => [producto._id, producto]);  
    return BDDEnArray;
}

const crearOrdeDeCompra = async (obj) => {
    const respJSON = await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/db?option=addOrder`,
            {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',         //Si los datos a enviar son en formato json
                },
                body: JSON.stringify(obj),
                credentials: "include"                          //Para poder recibir cookies
            }
        );

    const order = await respJSON.json();
    return order;
}

