import React from 'react';
import { useContext } from 'react';
import  { ShowContext } from "../../context/ShowContext"

const Error404 = () => {
    const {show} = useContext(ShowContext);             //El valor de la variable "show" se maneja desde App.jsx  y se actualiza despues de verificar si el usuario está logueado
    return (
        <div className="main__container flex">
            {show && <h2>Ruta no Válida</h2>}
        </div>
    );
}

export default Error404;
