import React, { useState, useEffect, useContext } from 'react';
import { createRoot } from 'react-dom/client';
import "./adminPanel.css";
import { leerBDD } from '../../utils/mongo';
import { SpinnerAzul } from '../main/Spinner';
import { actualizarProductoEnBDD } from '../../utils/mongo';
import { subirBDD, borrarProducto, agregarProducto, clearCarts } from '../../utils/mongo';
import { mensaje } from '../../utils/funcionesUtiles';
import { verifyLog } from '../../utils/verifyLog';
import { UserDataContext } from "../../context/UserDataContext";
import { CarritoContext } from "../../context/CarritoContext";

const rootInstances = new Map();        

const renderRoot = (container, elementToRender) => {
    if (!rootInstances.has(container)) {                // Si no existe una instancia para el elemento, crea una nueva
        const rootInstance = createRoot(container);
        rootInstances.set(container, rootInstance);
    }
    rootInstances.get(container).render(elementToRender);
};

const verificarColumnasOpcionesDeBusqueda = (celda, isAddFile) => {
    let opcionesDeBusquedaOBJ = celda.parentNode.querySelector(".columnaOpcionesBusquedaAdmin").innerText;
    typeof opcionesDeBusquedaOBJ === "string" ? opcionesDeBusquedaOBJ = JSON.parse(opcionesDeBusquedaOBJ) : opcionesDeBusquedaOBJ = JSON.parse(opcionesDeBusquedaOBJ.firstChild.innerText)

    const celdas = celda.parentNode.childNodes;
    const opcionesDeBusquedaEnString = opcionesDeBusquedaOBJ.join(" - ");

    const equivalencias = {
        Marca: "marca",
        Socket: "socket",
        Frecuencia: "frecuencia",
        "Tipo de Memoria": "tipodememoria",
        Modelo: "modelo",
        Rgb: "rgb",
        "Cantidad de Memoria": "cantidaddememoria",
        Certificacion: "certificacion",
        TDP: "tdp",
        Serie: "serie",
        Potencia: "potencia",
    }        

    const arrayNombresColumnas = opcionesDeBusquedaOBJ.map((opcion) => equivalencias[opcion]);
    const arrayNombresColumnasString = arrayNombresColumnas.join(" ");

    const valoresIngresados = [];
    for (const celda of celdas) {
        const nombreColumna = celda.getAttribute("field");
        if (arrayNombresColumnasString.includes(nombreColumna)) {
            let valorDeCelda;
            if (!isAddFile) {
                celda.querySelector("p") ? valorDeCelda = celda.firstChild.innerText : valorDeCelda = celda.lastChild.value;           //Si hay una etiqueta p no es el campo actual que stá siendo editado
            } else {
                valorDeCelda = celda.firstChild.value;  
            }     
            
            valoresIngresados.push(valorDeCelda);
        }        
    }
        
    const funcionDeVerificacion = (valor, indice, array) => {
        return valor && array.indexOf(valor) === indice;
    }

    const result = valoresIngresados.every(funcionDeVerificacion);     

    return {result, opcionesDeBusquedaEnString};
}

const AdminPanel = () => {

    const {guardarCarrito} = useContext(CarritoContext);
    const {saveUserData} = useContext(UserDataContext);
    const [listado, setListado] = useState(null);
    const [show, setShow] = useState(false);
    const [productAdded, setProductAdded] = useState(null);
            
    const addListeners = (celda) => {                                           //Agrega listeners en iconos cancel y ok
        celda.firstChild.addEventListener("click", function click(e) {
            celda.firstChild.removeEventListener("click", click)
            editarCampo(e.target.parentNode);
        })

        celda.addEventListener("click", function click(e) {
            celda.removeEventListener("click", click)
            editarCampo(e.target);
        })
    }

    const parseDataInput = (field, value) => {
        if(field === "opcionesBusqueda") value = JSON.parse(value);                         //Pasamos opciones de busqueda a OBJETO ya que en la tabla está como un string JSON    
        if(field === "stock" && typeof value === "string") value = parseInt(value);         //Pasamos stock a entero ya que en la tabla está como string
        if(field === "precio") {
            value = value.replace("$", "");
            value = parseInt(value);
        }
        return value;
    }

    const listaCategorias = {
        Microprocesadores: '[ "Marca", "Socket", "Frecuencia" ]',
        Motherboards: '[ "Marca", "Socket", "Tipo de Memoria" ]',
        memorias_ram: '[ "Marca", "Modelo", "Rgb", "Tipo de Memoria", "Cantidad de Memoria", "Frecuencia" ]',
        placas_de_video: '[ "Marca", "Serie", "Tipo de Memoria", "Cantidad de Memoria" ]',
        Fuentes: '[ "Marca", "Certificacion", "Potencia" ]',
        air_coolers: '[ "Marca", "TDP" ]',
        water_coolers: '[ "Marca", "TDP" ]'
    }

    const inputListaDeCategorias =
    <select className='selectCategoria' onChange={(e) => setOpcionesBusqueda(e)}>
        <option value="Microprocesadores">Microprocesadores</option>
        <option value="Motherboards">Motherboards</option>
        <option value="memorias_ram">Memorias RAM</option>
        <option value="placas_de_video">Placas de Video</option>
        <option value="Fuentes">Fuentes</option>
        <option value="air_coolers">Air Coolers</option>
        <option value="water_coolers">Water Coolers</option>
    </select>;     
          
    const editarCampo = (celda) => {

        if (celda.getAttribute("field") !== "categoria") {
            celda.innerHTML += `<img src="/images/check.png" alt="check" class="checkAdmin" />`;
            celda.innerHTML += `<img src="/images/cancel.png" alt="cancel" class="checkAdmin cancelAdmin" />`;

            const contenido = celda.firstChild.innerText;                   //Guardo y reemplazo el contenido de la celda
            celda.firstChild.remove();
            celda.innerHTML += `<textarea class='inputCeldasAdmin'/>${contenido}</textarea>`;
            const input = celda.lastChild;
            input.focus();                                                          //Insertamos cursor en el input
            input.setSelectionRange(input.value.length, input.value.length);        //Movemos el cursor al final del texto

            const check = celda.firstChild;
            check.addEventListener("click", (e) =>{
                e.stopPropagation();

                const result = verificarColumnasOpcionesDeBusqueda(celda);
                if (!result.result) {
                    mensaje(`Los campos: ( ${result.opcionesDeBusquedaEnString} ) no pueden estar vacíos y no pueden repetirse`, "hold");
                    return;
                }

                const contenidoInput = e.target.nextSibling.nextSibling.value;

                if(!contenidoInput) {                                               //Verificamos que el campo editdo no quede vacío
                    mensaje("El Campo no puede estar vacío", "hold");
                    input.value = contenido;
                    return;    
                }    

                input.remove();
                e.target.parentNode.innerHTML = `<p>${contenidoInput}</p>`;
                addListeners(celda);          

                const id = celda.parentNode.getAttribute("id");
                const field = celda.getAttribute("field");
                const value = contenidoInput;
                
                const parsedValue = parseDataInput(field, value);
                            
                actualizarProductoEnBDD(id, field, parsedValue || value)  
            })

            const cancel = celda.firstChild.nextSibling;
            cancel.addEventListener("click", (e) =>{
                e.stopPropagation();
                input.remove();
                e.target.parentNode.innerHTML = `<p>${contenido}</p>`;
                addListeners(celda);            
            })

        } else {                //Si estamos editando la columna categoria:
            
            const checkIcon = <img src="/images/check.png" alt="check" className="checkAdmin" />;
            const cancelIcon = <img src="/images/cancel.png" alt="cancel" className="checkAdmin cancelAdmin" />;

            const contenidoCategoriaCopia = celda.firstChild.innerText;
            const selectCategoria = 
            <>
                {checkIcon}
                <select className='selectCategoria' onChange={(e) => setOpcionesBusqueda(e)}>
                    <option value="Microprocesadores">Microprocesadores</option>
                    <option value="Motherboards">Motherboards</option>
                    <option value="memorias_ram">Memorias RAM</option>
                    <option value="placas_de_video">Placas de Video</option>
                    <option value="Fuentes">Fuentes</option>
                    <option value="air_coolers">Air Coolers</option>
                    <option value="water_coolers">Water Coolers</option>
                </select> 
                {cancelIcon}
            </>
           
            renderRoot(celda, selectCategoria);
            
            const contenidoOpcionesCopia = celda.parentNode.querySelector(".columnaOpcionesBusquedaAdmin").innerText;
            celda.parentNode.querySelector(".columnaOpcionesBusquedaAdmin").innerText = '[ "Marca", "Socket", "Frecuencia" ]';

            setTimeout(() => {          //Para esperar al metodo render de la funcion renderRoot
                const check = celda.firstChild;
                check.addEventListener("click", async (e) => {
                    e.stopPropagation();

                    const result = verificarColumnasOpcionesDeBusqueda(celda);
                    if (!result.result) {
                        mensaje(`Los campos: ( ${result.opcionesDeBusquedaEnString} ) no pueden estar vacíos y no pueden repetirse`, "hold");
                        return;
                    }

                    const opcionCategoria = celda.querySelector("select").value;            //Seleccinamos la primer etiqueta "select" encontrada
                    const opcionBusquedas = celda.parentNode.querySelector(".columnaOpcionesBusquedaAdmin").innerText;
                    renderRoot(celda, <p>{opcionCategoria}</p>);

                    const id = celda.parentNode.getAttribute("id");
                                                                                                                                        
                    await actualizarProductoEnBDD(id, "categoria", opcionCategoria);  
                    await actualizarProductoEnBDD(id, "opcionesBusqueda", JSON.parse(opcionBusquedas));    

                    addListeners(celda);
                })

                const cancel = celda.lastChild;
                cancel.addEventListener("click", (e) => {
                    e.stopPropagation();
                    renderRoot(celda, <p>{contenidoCategoriaCopia}</p> );
                    celda.parentNode.querySelector(".columnaOpcionesBusquedaAdmin").innerText = contenidoOpcionesCopia;
                    setTimeout(() => {
                        addListeners(celda);    //Para esperar al metodo render de la funcion renderRoot
                    }, 0);
                    
                })
            }, 0);
        }
    }       
          
    const listarProductos = async () => {
        if (productAdded) setProductAdded(null);     //Si estamos agregando un producto y no lo guardamos lo borramos

        setListado(null)
                               
        setShow(true)                                                               /* Filas Productos */
        const productos = await leerBDD();
        const productosEnFilas = productos.map((producto, index) => 
            <tr key={producto[0]} id={producto[0]}>
                <td><p>{index + 1}</p><div className='contDeleteAdmin'><img src="/images/cancel.png" alt="delete" className="checkAdmin deleteAdmin" /></div></td>
                <td field="categoria"><p>{producto[1].categoria}</p></td>
                <td field="marca"><p>{producto[1].marca}</p></td>
                <td field="modelo"><p>{producto[1].modelo}</p></td>
                <td field="precio"><p>${producto[1].precio}</p></td>
                <td ><img src={producto[1].imgScr} alt="imagen Producto" className='imgProductosAdmin'/></td>
                <td field="imgScr" className='columnaReducidaAdmin'><p>{producto[1].imgScr}</p></td>
                <td field="stock"><p>{producto[1].stock}</p></td>
                <td field="socket"><p>{producto[1].socket}</p></td>
                <td field="frecuencia"><p>{producto[1].frecuencia}</p></td>
                <td field="tipodememoria"><p>{producto[1].tipodememoria}</p></td>
                <td field="cantidaddememoria"><p>{producto[1].cantidaddememoria}</p></td>
                <td field="tdp"><p>{producto[1].tdp}</p></td>
                <td field="serie"><p>{producto[1].serie}</p></td>
                <td field="certificacion"><p>{producto[1].certificacion}</p></td>
                <td field="potencia"><p>{producto[1].potencia}</p></td>
                <td field="rgb"><p>{producto[1].rgb}</p></td>
                <td field="opcionesBusqueda" className='columnaReducidaAdmin columnaOpcionesBusquedaAdmin'><p>{JSON.stringify(producto[1].opcionesBusqueda, null, 1)}</p></td>
                <td field="describir"><p>{producto[1].describir}</p></td>    
            </tr>
        )
        
        setShow(false)
        setListado(productosEnFilas);
        localStorage.setItem("listado", true);                              //Guardamos un flag en localstorage indicando que hay una lista desplegada
    }

    useEffect(() => {
        if (listado) {
            const celdas = document.getElementsByTagName("td");
            for (const celda of celdas) {
                if (celda.getAttribute("field") && celda.getAttribute("field") !== "opcionesBusqueda") {                              //Solo editables las celdas con atributo "field"
                    celda.addEventListener("click", function click (e) {                                                              //Agregamos listeners en celdas
                        celda.removeEventListener("click", click)
                        editarCampo(e.target);
                    })
                }    
            }
            
            for (const celda of celdas) {
                if (celda.getAttribute("field") && celda.getAttribute("field") !== "opcionesBusqueda") {                             //Solo editables las celdas con atributo "field"    
                    const input = celda.firstChild;                                                                                 //Agregamos listeners en textos de celdas
                    input.addEventListener("click", function click (e) {
                        input.removeEventListener("click", click)
                        editarCampo(e.target.parentNode)
                    })
                }
            }        

            const iconosDelete = document.getElementsByClassName("deleteAdmin");
            for (const icono of iconosDelete) {
                icono.addEventListener("click", async (e) => {
                    const id = e.target.parentNode.parentNode.parentNode.id;
                    await borrarProducto(id);
                    listarProductos();
                })
            }

            const iconoAdd = document.querySelector(".addIconAdmin");           //Fila de producto a Agregar
            iconoAdd.addEventListener("click", (e) => {
                setProductAdded(
                    <tr className="newTr">
                        <td>
                            <div className='contDeleteAdmin contDeleteAdmin-Ok'>
                                <img src="/images/check.png" alt="delete" className="checkAdmin deleteAdmin" onClick={(e) => addProductInDB(e)} />
                            </div>                            
                            <div className='contDeleteAdmin contDeleteAdmin-cancel'>
                                <img src="/images/cancel.png" alt="delete" className="checkAdmin deleteAdmin" onClick={() => setProductAdded(null)}/>
                            </div>
                        </td>
                        <td field="categoria">{inputListaDeCategorias}</td>
                        <td field="marca"><textarea className="inputCeldasAdmin inputCeldasAddProduct"></textarea></td>
                        <td field="modelo"><textarea className="inputCeldasAdmin inputCeldasAddProduct"></textarea></td>
                        <td field="precio"><textarea className="inputCeldasAdmin inputCeldasAddProduct"></textarea></td>
                        <td ></td>
                        <td field="imgScr" className='columnaReducidaAdmin'><textarea className="inputCeldasAdmin inputCeldasAddProduct"></textarea></td>
                        <td field="stock"><textarea className="inputCeldasAdmin inputCeldasAddProduct"></textarea></td>
                        <td field="socket"><textarea className="inputCeldasAdmin inputCeldasAddProduct" defaultValue="NA"></textarea></td>
                        <td field="frecuencia"><textarea className="inputCeldasAdmin inputCeldasAddProduct" defaultValue="NA"></textarea></td>
                        <td field="tipodememoria"><textarea className="inputCeldasAdmin inputCeldasAddProduct" defaultValue="NA"></textarea></td>
                        <td field="cantidaddememoria"><textarea className="inputCeldasAdmin inputCeldasAddProduct" defaultValue="NA"></textarea></td>
                        <td field="tdp"><textarea className="inputCeldasAdmin inputCeldasAddProduct" defaultValue="NA"></textarea></td>
                        <td field="serie"><textarea className="inputCeldasAdmin inputCeldasAddProduct" defaultValue="NA"></textarea></td>
                        <td field="certificacion"><textarea className="inputCeldasAdmin inputCeldasAddProduct" defaultValue="NA"></textarea></td>
                        <td field="potencia"><textarea className="inputCeldasAdmin inputCeldasAddProduct" defaultValue="NA"></textarea></td>
                        <td field="rgb"><textarea className="inputCeldasAdmin inputCeldasAddProduct" defaultValue="NA"></textarea></td>
                        <td field="opcionesBusqueda" className='columnaReducidaAdmin columnaOpcionesBusquedaAdmin'>[ "Marca", "Socket", "Frecuencia" ]</td>
                        <td field="describir"><textarea className="inputCeldasAdmin inputCeldasAddProduct"></textarea></td>    
                    </tr>     
                )                                   
            })            
        }
        // eslint-disable-next-line
    }, [listado]);          //"listado" es la tabla que se genera al leer la base de datos de productos, al terminar de cargarse corremos el "useeffect"
                      
    useEffect(() => {
        if (localStorage.getItem("listado")) {                            //Si habia un listado desplegado lo mostramos  
            const hayListado = JSON.parse(localStorage.getItem("listado"));
            if (hayListado) listarProductos();
        }
        // eslint-disable-next-line
    }, []);      

    const subirBase = async() => {
        await subirBDD();
        await clearCarts();
        const resp = await verifyLog();
        saveUserData(resp.username, resp.isLogged, resp.url_avatar, resp.carrito, resp.isadmin);
        guardarCarrito(resp.carrito);
        setTimeout(() => {
            listarProductos();
        }, 1000);
    }

    const addProductInDB = async(e) => {
        const row = e.target.parentNode.parentNode.parentNode;
        const cells = row.childNodes;
        const productDataOBJ = {};
        for (const cell of cells) {
            if(cell.getAttribute("field")) {
                const field = cell.getAttribute("field");
                let valor;                
                
                if (field !== "categoria" && field !== "opcionesBusqueda") {
                    valor = cell.querySelector(".inputCeldasAdmin").value;
                    if(!valor) {
                        mensaje("Faltan Ingresar datos", "hold")
                        return;    
                    }   
                }                

                if (field === "categoria") valor = cell.querySelector(".selectCategoria").value;
                                        
                if (field === "opcionesBusqueda") valor = cell.innerText;

                const parsedValue = parseDataInput(field, valor);

                productDataOBJ[field] = parsedValue || valor;                       //Construimos un objeto de producto con los datos ingresados
            }
        } 

        const celda = e.target.parentNode.parentNode;
        const result = verificarColumnasOpcionesDeBusqueda(celda, true);
        if (!result.result) {
            mensaje(`Los campos: ( ${result.opcionesDeBusquedaEnString} ) no pueden estar vacíos y no pueden repetirse`, "hold");
            return;
        }

        agregarProducto(productDataOBJ);
    }

    const setOpcionesBusqueda = (e) => {
        e.target.parentNode.parentNode.querySelector(".columnaOpcionesBusquedaAdmin").innerText = listaCategorias[e.target.value];
    }
      
    return (
        <div className='contGen contAdmin flex'>
            <div className='adminOptionsPanel flex'>
                <button className="botonesAuth botonesAdmin botonListar" onClick={listarProductos}>Listar Productos</button>
                <button className="botonesAuth botonesAdmin botonBDD" onClick={subirBase}>Restaurar Base de Datos de Productos</button>
            </div>
            
            {                                                 /*Cabecera de Tabla */
                (
                    listado &&
                    <table className='allProductsTable'>                
                            <thead>
                                <tr>
                                    <th className='celdaAdd'><div className='contIconAdd'><img src="/images/add.png" alt="add icon" className='addIconAdmin'/></div></th>
                                    <th>Categoria</th>
                                    <th>Marca</th>
                                    <th>Modelo</th>
                                    <th>Precio</th>
                                    <th>Imagen</th>
                                    <th className='columnaReducidaAdmin'>Url Imagen</th>
                                    <th>Stock</th>
                                    <th>Socket</th>
                                    <th>Frecuencia</th>
                                    <th>Tipo de Memoria</th>
                                    <th>Cantidad de Memoria</th>
                                    <th>TDP(W)</th>
                                    <th>Serie</th>
                                    <th>Certificación</th>
                                    <th>Potencia</th>
                                    <th>RGB</th>
                                    <th className='columnaReducidaAdmin columnaOpcionesBusquedaAdmin'>Opciones de Búsqueda (JSON)</th>
                                    <th>Descripción</th>
                                </tr>
                            </thead>
                            <tbody className='bodyTableAdmin'>
                                {productAdded}
                                {listado}
                            </tbody>
                    </table>
                )
                ||
                (   
                    !listado && show && <SpinnerAzul/>
                )
            }

        </div>
    );
}

export default AdminPanel;
