import Swal from "sweetalert2";

const mensaje = (mensaje, opcion) => {
    let buttonText = "";
    
    if (opcion === "back") {
        buttonText = "VOLVER"
    } else if (opcion === "hold") {
        buttonText = "OK"
    } else {
        buttonText = "HOME";
    }
      
    Swal.fire({
        title: mensaje,
        icon: 'warning',
        confirmButtonColor: '#3085d6',
        confirmButtonText: buttonText,
        allowOutsideClick: false,
        allowEscapeKey: false,
        scrollbarPadding: false
    })  
    .then((result) => {
        if (result.isConfirmed) {
            if (opcion === "back") {
                window.history.back() 
            } else if (opcion === "hold") {

            }  else  {
                window.location.href = "/";
            }
        }
    })
}

const mensajeOK = (msj) => {
    Swal.fire({
        position: 'top',
        width: 400,
        icon: 'success',
        title: msj,
        showConfirmButton: false,
        timer: 2000,
        scrollbarPadding: false
    })
}

export {mensaje, mensajeOK};