import React from 'react';
import { Link } from 'react-router-dom';
import { UserDataContext } from "../../context/UserDataContext";
import { mensaje } from '../../utils/funcionesUtiles';
import { useContext } from 'react';

const CardProducto = ({producto}) => {

    const {userData} = useContext(UserDataContext);

    return (
        <div className="cardProducto flex">
            <img className="imgProducto" src={producto[1].imgScr} alt=""/>
            <div className='descProducto'>{producto[1].describir}</div>
            {userData.isLogged && <Link to={`/item/${producto[0]}`}> <button className='botonProducto'>Ver Detalles</button> </Link>}
            {!userData.isLogged && <div onClick={() => mensaje("Necesitas Loguearte para Continuar")}> <button className='botonProducto'>Ver Detalles</button> </div>}
            <div className='precioProducto'>${producto[1].precio}</div>
        </div>
    );
}

export default CardProducto;
