import {useState, createContext} from "react";

const UserDataContext = createContext();

const UserDataProvider = ({children}) => {
    
    const [userData, setUserData] = useState({username: null, isLogged: false, url_avatar: null, carrito: [], isadmin: false});
    
    const saveUserData = (username, isLogged, url_avatar, carrito, isadmin) => {
        setUserData({username: username, isLogged: isLogged, url_avatar: url_avatar, carrito: carrito, isadmin: isadmin});        //en la variable global en contexto "useData" se guarda el nombre de usuario y el rol
    }        
        
    return (
        <UserDataContext.Provider value={{saveUserData, userData}}>
            {children}
        </UserDataContext.Provider>
    );
}

export {UserDataContext, UserDataProvider};