const verifyLog = async () => {
    const respJSON = await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/islogged/`,
        {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: "include"                          //Para poder recibir cookies
        })
    const respOBJ = await respJSON.json();
          
    return ({username: respOBJ.username, isLogged: respOBJ.isLogged, url_avatar: respOBJ.url_avatar, carrito: respOBJ.carrito, isadmin: respOBJ.isadmin})
}

export {verifyLog}