import "./chat.css"
import { useState, useEffect, useContext } from 'react';
import { UserDataContext } from "../../context/UserDataContext";
const io = require('socket.io-client');
let client;

const Chat = () => {
    const {userData} = useContext(UserDataContext);
    const [mensajes, setMensajes] = useState([]);
    const [showChat, setShowChat] = useState(false);
    const [mensajeInput, setMensajeInput] = useState("");
    
    useEffect(() => {
        client = io(process.env.REACT_APP_HOST_SERVER);
        client.on("all-messages", (listaDeMensajes) => {
            let key = 0;
            const mensajesHtml = listaDeMensajes.map((mensaje) => {
                key++;
                return <p key={key} className="chatMensajes"> <span className="chatMensajeUsername">{mensaje.username}</span> <span className="chatMensajeFecha">({mensaje.date})</span>: <br/> {mensaje.message} </p>
            })
            setMensajes(mensajesHtml);
        }); 
    }, []); 

    const scrollBottom = () => {
        const contMensajes = document.querySelector(".chatMensajesCont");       //Mueve el scroll del div de mensajes hacia abajo
        if (contMensajes) {
            contMensajes.scrollTo(0, contMensajes.scrollHeight);
        }
    }

    useEffect(() => {
        scrollBottom();
    }, [mensajes, showChat]);

    const sendMsj = () => {
        const mensajeTexto = mensajeInput;
        if (mensajeTexto.trim()) {
            const mensajeCompuesto = {username: userData.username, message: mensajeTexto, date: new Date().toLocaleString()};
            setMensajeInput("");
            client.emit("mensaje-del-cliente", mensajeCompuesto);
        }
    }
    
    const handleInput = (e) => {
        setMensajeInput(e.target.value)
    }
    
    return (
        <>
        {
            (showChat  &&  
            <div className='chatCont flex column'>
                    <div className='chatBox'>
                        <img className="chatMinimize" src="/images/minimize.png" alt="minimize" onClick={() => setShowChat(false)}/>
                        <div className='chatMensajesCont'>
                            {mensajes}
                        </div>
                        <div className='chatMensajeCont flex'>
                            <input className='chatInputMensaje' type="text" value={mensajeInput} onChange={(e) => handleInput(e)}/>
                            <button className='chatButtonSend' type='button' onClick={(e) => sendMsj(e)}>Enviar</button>
                        </div>
                    </div>
                </div>)            
            ||            
            (!showChat && <img className="bubble" src="/images/bubble.png" alt="chat" onClick={() => setShowChat(true)} />)
        }
        </>
    );
}

export default Chat;
