import React from 'react';
import Swal from 'sweetalert2';
import { useRef, useContext } from 'react';
import {UserDataContext} from "../../context/UserDataContext"
import { useNavigate } from 'react-router-dom';
import "./signup.css";
import { mensaje } from '../../utils/funcionesUtiles';

const SignUp = () => {
    const navigate = useNavigate();
    const formRef = useRef()
    const {saveUserData, userData} = useContext(UserDataContext);
    const isLogged = userData.isLogged;
           
    const signUp = async (newFileName) => {

        const formData = new FormData(formRef.current); 
        const data = Object.fromEntries(formData);
        data.filename = newFileName;
                                           
        try {
            const respJSON = await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/signup/`,
                {
                    method: 'POST', 
                    headers: {
                        'Content-Type': 'application/json',         //Si los datos a enviar y recibir son en formato json
                    },
                    body: JSON.stringify(data),                     //data es un objeto con las keys iguales a los atributos "name" del formulario

                    credentials: "include"                          //Para poder recibir cookies
                }
            );
            const resolution = await respJSON.json();
                                    
            if (resolution.isLogged) {
                saveUserData(resolution.username, resolution.isLogged, resolution.url_avatar, resolution.carrito);
                navigate("/");
                
            } else if (resolution.error) {
                Swal.fire({
                    icon: 'error',
                    title: 'El Usuario Ya Existe',
                    text: 'Intenta nuevamente',
                })
            }

        } catch (err) {
            Swal.fire({
                icon: 'error',
                title: 'Error al crear usuario: ' + err,
                text: 'Intenta nuevamente',
            })
        }
    }

    const uploadFile = async () => {

        const file = document.querySelector(".addFile").files[0];
        const formData = new FormData();
        formData.append("file", file);

        const respJSON = await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/upload/`,
            {
                method: 'POST', 
                body: formData,                     //data es un objeto con las keys iguales a los atributos "name" del formulario
            }
        );
        const resp = await respJSON.json();
        return resp.filename;
    }

    const send = async () => {
        const formData = new FormData(formRef.current); 
        const data = Object.fromEntries(formData);

        const regexMail = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        const regexPhone = /^\+(?:[0-9] ?){6,14}[0-9]$/;
                  
        if (data.username.trim() !=="" && data.password.trim() !=="" && data.name.trim() !=="" && data.address.trim() !=="" && data.age.trim()!=="" && data.phone.trim() !=="") {
            if(!regexMail.test(data.username)) {
                mensaje("E-Mail No válido", "hold");
                return
            }
            if(!regexPhone.test(data.phone)) {
                mensaje("Teléfono Inválido, tiene que empezar con '+' y tener formato internacional. Ejemplo: +5491140869823 Para Argentina", "hold");
                return
            }
            const newFileName = await uploadFile();
            await signUp(newFileName);
        } else {
            mensaje("Faltan Ingresar Datos", "hold");
        }   
    }

    const addFile = (e) => {
        const botonAddFile = e.target;
        const inputFile = e.target.nextSibling;
        inputFile.click();
        
        inputFile.onchange = (e) => {
            const file = e.target.files[0];
            if (file.size > 1024 * 100) {           //Maximo 100KBytes
                inputFile.value = "";    
                Swal.fire({
                    icon: 'error',
                    title: 'El Archivo Supera 1MB',
                    text: 'Intenta nuevamente',
                })
            } else {
                botonAddFile.innerText = file.name;
            }
        }
    }

    return (
        <div className='contGen flex'>
        { !isLogged &&
            <div className='contForm flex column'>
                <form className='flex column' ref={formRef}>
                    <input type="email" name="username" placeholder='email'/>
                    <input type="password" name="password" placeholder='password'/>
                    <input type="text" name="name" placeholder='nombre'/>
                    <input type="text" name="address" placeholder='dirección'/>
                    <input type="text" name="age" placeholder='edad'/>
                    <input type="text" name="phone" placeholder='teléfono'/>
                </form>

                <button type='button' className='botonAddFile' onClick={addFile}>Seleccionar Imagen de Avatar <br /> (Maximo: 100KB)</button>
                <input type="file" name="avatar_file" className='addFile'/>
                <p className='notaAvatar'>Si no se selecciona ninguna imagen se cargará un avatar por defecto.</p>

                <button type='button' className='botonEnviar' onClick={send}>SignUp</button>
            </div>
        }
        { isLogged &&
            <h1>Ya estas Logueado</h1>
        }
        </div>
    );
}

export default SignUp;

