const logOut = async () => {
    const respJSON = await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/logout/`,
        {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
            credentials: "include"                          //Para poder recibir cookies
        })
    const respOBJ = await respJSON.json();
       
    return ({username: respOBJ.username, isLogged: respOBJ.isLogged})
}

export {logOut}