import React, { useState, useEffect} from 'react';
import {useParams} from "react-router-dom";
import { leerProducto } from '../../utils/mongo';
import CardProductoDetail from './CardProductoDetail';
import {Spinner} from './Spinner';

const ItemDetailContainer = () => {
    
    const {id} = useParams();
    const [producto, setProducto] = useState ([<Spinner key={"spinner"}/>]);
    
    useEffect(() => {
        
        leerProducto(id).then((prod) => {
            !prod[0] ? setProducto(<h2>Producto No encontrado</h2>) : setProducto(<CardProductoDetail producto={prod}/>);       //Si prod[0] (id de producto encontrado) es undefined es porque el producto no se encontró con el id pasado como parámetro en la URL
        });
                
    },[id]);

    return (
        <>  
            <div className="main__container flex">
                {producto}
            </div>
        </>
    );
}

export default ItemDetailContainer;
