import Swal from 'sweetalert2';
import { useRef, useContext } from 'react';
import { UserDataContext } from "../../context/UserDataContext"
import { CarritoContext } from "../../context/CarritoContext"
import { useNavigate } from 'react-router-dom';

const LogIn = () => {
    const {guardarCarrito} = useContext (CarritoContext);
    const navigate = useNavigate();
    const formRef = useRef()
    const {saveUserData, userData} = useContext(UserDataContext);
    const isLogged = userData.isLogged;
           
    const login = async () => {

        const formData = new FormData(formRef.current);
        const data = Object.fromEntries(formData);

        try {
            const respJSON = await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/login/`,
                {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',         //Si los datos a enviar y recibir son en formato json
                    },
                    body: JSON.stringify({ username: data.username, password: data.password }),
                    credentials: "include"                          //Para poder recibir cookies
                }
            );

            const resolution = await respJSON.json();
        
            if (resolution.isLogged) {
                saveUserData(resolution.username, resolution.isLogged, resolution.url_avatar, resolution.carrito, resolution.isadmin);
                guardarCarrito(resolution.carrito);
                navigate("/");
            }    

        } catch (err) {
            if  (err.toString().includes("Unauthorized")) {
                Swal.fire({
                    icon: 'error',
                    title: 'Credenciales Invalidas',
                    text: 'Intenta nuevamente',
                })
            }
        }
    } 
                    
    return (
        <div className='contGen flex'>
            { !isLogged &&
                <form className='flex column' ref={formRef}>
                    <input type="email" name="username" placeholder='email'/>
                    <input type="password" name="password" placeholder='password'/>
                    <button type='button' className='botonEnviar' onClick={login}>LogIn</button>
                </form>
            }
            { isLogged &&
                <h1>Ya estas Logueado</h1>
            }
        </div>
    );
}

export default LogIn;

