import {useState, createContext} from "react";                  

const ShowContext = createContext();

const ShowContextProvider = ({children}) => {
    
    const [show, setShow] = useState(false);            //La variable show oculta o muestra e panel de logueo en el navbar (ver componente: NavBarUsers.jsx y App.jsx)...
                                                        //para que no aparezca por un instante los botones login y signup al recargar la pagina si estamos logueados
    const showOn = (opc) => {                  
        setShow(opc);
    }
        
    return (
        <ShowContext.Provider value={{show, showOn}}>
            {children}
        </ShowContext.Provider>
    );
}

export {ShowContext, ShowContextProvider};