import React from 'react';
import { useRef, useContext, useEffect, useState} from 'react';
import Swal from 'sweetalert2';
import { CarritoContext } from '../../context/CarritoContext';
import { UserDataContext} from "../../context/UserDataContext";
import { crearOrdeDeCompra } from '../../utils/mongo';
import { useNavigate } from 'react-router-dom';
import { getUser, leerProducto } from '../../utils/mongo';

const Form = ({toggleForm}) => {

    const {userData} = useContext(UserDataContext);
    const {carritoTotal, vaciarCarrito, carrito} = useContext (CarritoContext);
    const formulario = useRef();
    const navigate = useNavigate();
    window.scrollTo({top:(document.body.offsetHeight/2)-(window.innerHeight/2)});           //Movemos el scroll hasta centrar el form en pantalla
    const [user, setUser] = useState([]);

    useEffect(() => {
        const loadData = async () => {
            const userInfo = await getUser(userData.username);
            setUser(userInfo);
        }
        loadData();        
        // eslint-disable-next-line
    }, []);

    const listaDeProductosOrden = async () => {
        const lista = [];
        for (const prod of carrito) {
            const producto = await leerProducto(prod.id);
            lista.push({producto: producto[1].describir, cantidad: prod.cantidad})
        }
        return lista;
    }    

    const enviarOrden = async () => {
        const productosOrden = await listaDeProductosOrden();

        let productosEnString = "";
        for (const producto of productosOrden) {
            productosEnString += `\n\n${producto.producto} -- ${producto.cantidad} Unidades`
        }
                
        const orden = {
            date: new Date().toLocaleString(),
            username: user.username,
            name: user.name,
            address: user.address,
            phone: user.phone,
            products: productosEnString,
            total: carritoTotal
        }
               
        crearOrdeDeCompra(orden).then((data) => {
            vaciarCarrito(false);
            navigate("/");
            document.body.style.overflow = "visible";
            const ordenId = data._id;
            const orderShowToUser = ordenId.slice(-6);      //Mostramos el usuario los ultimos 6 caracteres de la ID de la orden
            Swal.fire({
                title: 'Gracias por Elegirnos!',
                text: `Tu orden de compra es la Nº: ${orderShowToUser}`,
                icon: 'success',
                confirmButtonColor: '#3085d6',
                confirmButtonText: 'OK',
                scrollbarPadding: false
            })
        });
              
    }     
 
    return (
        <>
            <div className='cont_formulario flex' id="cont_formulario" onClick={((e) => toggleForm(e))} style={{height:document.body.offsetHeight+"px", top:0}}>
                <img className='closeForm' src="../images/close.png" alt="" id="close" onClick={toggleForm}/>        
                <form className="flex form" ref={formulario} >
                    <h1 className="formulario_titulo">Datos de la Orden de Compra:</h1>
                    <label className='label flex'>Nombre</label>
                    <div className="input" type="text" name="nombre">{user.name}</div>
                    <label className='label'>E-Mail</label>
                    <div className="input" type="email" name="email" placeholder="E-Mail">{user.username}</div>
                    <label className='label'>Direccion</label>
                    <div className="input" type="text" name="direccion" placeholder="Dirección">{user.address}</div>
                    <label className='label'>Teléfono</label>
                    <div className="input" type="tel" name="telefono" placeholder="Teléfono">{user.phone}</div>
                    <label className='label'>Total</label>
                    <div className="input" type="tel" name="telefono" placeholder="Teléfono">${carritoTotal}</div>
                    <button className="formulario_boton flex" type="button" onClick={enviarOrden}>CONFIRMAR COMPRA</button>
                </form>
            </div>
        </>
    );
}

export default Form;
