import React from 'react';
import "./navBarUsers.css"
import { useContext } from 'react';
import { Link } from 'react-router-dom';
import { UserDataContext } from "../../context/UserDataContext";
import { logOut } from '../../utils/logOut';
import Avatar from '../avatar/Avatar';
import { useNavigate } from 'react-router-dom';
import { CarritoContext } from '../../context/CarritoContext';
import  { ShowContext } from "../../context/ShowContext"

const NavBarUsers = () => {
    const {userData, saveUserData} = useContext(UserDataContext);
    const isLogged = userData.isLogged;
    const isadmin = userData.isadmin;
    const navigate = useNavigate();
    const {vaciarCarrito} = useContext(CarritoContext);
    const {show} = useContext(ShowContext);
       
    const exit = async () => {
        const resp = await logOut();
        saveUserData(resp.username, resp.isLogged, resp.url_avatar, resp.carrito, resp.isadmin);
        vaciarCarrito(true);            //Vaciamos carrito sin guardarlo en la base de datos (true)
        navigate("/");
    }
     
    return (
        <div className='navBarUsers flex'>
            { !isLogged && show &&
            <div className='contBotonesLog flex'>
                <Link to="/login"><button className="botonesAuth">LogIn</button></Link>
                <Link to="/signup"><button className="botonesAuth">SignUp</button></Link>
            </div>
            }
            { isLogged && show &&
            <>
                <div className='contLogged flex'>
                    <div className='contAvatarYUser flex'>
                        <div className='conth1h2 flex column'>
                                <h2>Bienvenido</h2> 
                                <h1>{userData.username}</h1>
                        </div>
                        <Avatar/>
                    </div>
                    <button onClick={exit} className="botonesAuth">LogOut</button>
                    {isadmin && <Link to="/admin"><button className="botonesAuth">Admin Panel</button></Link>}
                </div>
            </>
            }
        </div>
    );
}

export default NavBarUsers;
