import React from 'react';
import Navbar from "./components/navbar/Navbar";
import ItemListContainer from './components/main/ItemListContainer';
import ItemDetailContainer from './components/main/ItemDetailContainer';
import ItemCategoryContainer from './components/main/ItemCategoryContainer';
import ItemSearchContainer from './components/main/ItemSearchContainer';
import Footer from './components/footer/Footer';
import Carrito from './components/main/Carrito';
import Error404 from './components/main/Error404';
import "./App.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import LogIn from './components/login/LogIn';
import SignUp from './components/signup/SignUp';
import { verifyLog } from './utils/verifyLog';
import { UserDataContext } from "./context/UserDataContext";
import { useContext, useEffect } from 'react';
import { CarritoContext } from "./context/CarritoContext";
import  { ShowContext } from "./context/ShowContext"
import AdminPanel from './components/adminPanel/AdminPanel';
import Chat from './components/chat/Chat';

const App = () => {
    const {saveUserData, userData} = useContext(UserDataContext);
    const {showOn} = useContext(ShowContext);
    const {guardarCarrito} = useContext(CarritoContext);
               
    useEffect(() => {
        const verify = async () => {
            const resp = await verifyLog()
            saveUserData(resp.username, resp.isLogged, resp.url_avatar, resp.carrito, resp.isadmin);
            guardarCarrito(resp.carrito);
            showOn(true);         //Una vez que verificamos si el usuario está logueado mostramos el panel de logueo y si corresponde el error 404
        }    
        verify();
        //eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        < >
            <BrowserRouter>
                <Navbar />
                {userData.isLogged && <Chat />}
                <Routes>
                    <Route path="/" element={<ItemListContainer />} /> 
                    <Route path="/login" element={<LogIn/>}/>
                    <Route path="/signup" element={<SignUp/>}/>
                    {userData.isLogged && <Route path="/cart" element={<Carrito />} />}
                    {userData.isLogged && <Route path="/item/:id" element={<ItemDetailContainer />} />}
                    {userData.isadmin && <Route path="/admin" element={<AdminPanel />} />}
                    <Route path="/category/:categoria" element={<ItemCategoryContainer />} />
                    <Route path="/category/:categoria/:filterKeys" element={<ItemCategoryContainer />} />
                    <Route path="/search/:searchKeys" element={<ItemSearchContainer />} />
                    <Route path="*" element={<Error404 />} />
                </Routes>    
                <Footer />
            </BrowserRouter>
        </>
    );
}

export default App;
