const guardarCarritoEnDB = async (carrito) => {
        
    await fetch(`${process.env.REACT_APP_HOST_SERVER}/api/db?option=saveCart`,
        {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',         //Si los datos a enviar y recibir son en formato json
            },
            body: JSON.stringify(carrito),                     

            credentials: "include"                          //Para poder recibir cookies
        }
    );
    
}

export default guardarCarritoEnDB;


